<template>
  <div id="send">
    <Gift />
    <Address />
    <Cell :title="'发货数量'" :type="'count'" :num="value" :max="5" @change="valueChange"/>
    <Cell :title="'留言'" :type="'input'" placeholder="选填"/>

    <div class="submit__btn" :class="{'active': !true}">确认发货</div>
  </div>
</template>

<script>
  import Gift from '@/components/Goods/Goods';
  import Address from '@/components/Address/Address';
  import Cell from '@/components/Cell/Cell';
  
  export default {
    data() {
      return {
        value: 2
      }
    },
    methods: {
      valueChange(val) {
        this.value += val;
      }
    },
    components: {
      Gift,
      Cell,
      Address
    }
  }
</script>


<style lang="scss">
  

  #send {
    height: 100%;
    background: #f0f0f0;

    #giftDetail__good {
      margin-top: 0;
      border-bottom: 1px solid #eee;
    }
  }
</style>




